import React, {useEffect, useState, useRef} from "react";
import Layout from "../components/layout";
import SEO from "../components/seo";
import { InteractivePage } from '../lib/3DPage.js';

const VIEWS = {
    'HOME': {
        id: 'HOME',
        content: `I am a front end web developer with a passion for good design, team focused, clear communicator, emphasizing cross team collaboration and transparency.`,
    },
    'TOOLS': {
        id: 'TOOLS',
        content: `Good design, team focused, clear communicator.`,
    }
};


const IndexPage = () => {
    const animationAreaEl = useRef(null);
    const [interactivePage, setInteractivePage] = useState(null);
    const [currentView, setCurrentView] = useState(VIEWS.HOME.id);
    const [currentText, setCurrentText] = useState(VIEWS.HOME.content);

    useEffect(() => {
        if (currentText) {
            if (!interactivePage) {
                const interactivePageInstance = new InteractivePage(currentText, animationAreaEl.current);
                setInteractivePage(interactivePageInstance);
                interactivePageInstance.run();
            } else {
                interactivePage.phrase = currentText;
            }
        }
    }, [currentText]);

    useEffect(() => {
        if (currentView) {
            setCurrentText(VIEWS[currentView].content);
        }
    }, [currentView]);

    return (
        <>
          <Layout>
            <SEO title="Animation" />
          </Layout>
          <div id="menu" className="d-flex flex-column">
            <button id="home" onClick={() => setCurrentView(VIEWS.HOME.id)}>Home</button>
            <button id="tools" onClick={() => setCurrentView(VIEWS.TOOLS.id)}>Tools</button>
          </div>
          <div className="animation-area dark" ref={animationAreaEl}/>
        </>
    );
};

export default IndexPage;
