import * as THREE from 'three.js';
import TWEEN from '@tweenjs/tween.js';
import { Letter } from './letter';

export class Phrase {
    objects = [];
    phrase = '';
    scene = null;
    limit = 38;
    renderCallback = null;

    constructor({scene, text = '', onUpdate}) {
        this.phrase = text;
        this.scene = scene;
        this.renderCallback = onUpdate;
        this._init();
        this._transform(2000);
    }

    _init() {
        let rowCounter = 1;
        let columnCounter = 0;
        this.objects = this.phrase.split('').map((l, idx) => {
            const hasReachedLimit = ((idx + 1) % this.limit) === 0 && idx !== 0;
            const column = hasReachedLimit ? columnCounter = 1 : columnCounter += 1;
            const row = hasReachedLimit ? rowCounter += 1 : rowCounter;
            const letter = new Letter({
                scene: this.scene,
                letter: l,
                column,
                row,
            });
            return letter.data;
        });
    }

    _executeCallback = () => {
        this.renderCallback && this.renderCallback();
    }

    _transform(duration) {
        TWEEN.removeAll();
        for (let index = 0; index < this.objects.length; index++) {
            const currentObject = this.objects[index];
            const object = currentObject.css3DObject;
            const target = currentObject.object3d;

            new TWEEN.Tween( object.position )
                .to({
                    x: target.position.x,
                    y: target.position.y,
                    z: target.position.z
                }, Math.random() * duration + duration )
                .easing( TWEEN.Easing.Exponential.InOut )
                .start();

            new TWEEN.Tween( object.rotation )
                .to({
                    x: target.rotation.x,
                    y: target.rotation.y,
                    z: target.rotation.z
                }, Math.random() * duration + duration )
                .easing( TWEEN.Easing.Exponential.InOut )
                .start();
        }

        new TWEEN.Tween( this )
            .to( {}, duration * 2 )
            .onUpdate(this._executeCallback)
            .start();
    }

    _cleanObjects() {
        this.objects.forEach(currentObj => {
            currentObj.instance.destroy();
        });

        this.objects = [];
    }

    updateText(text) {
        this.phrase = text;
        this._cleanObjects();
        this._init();
        this._transform(2000);
    }

    update() {
        TWEEN.update();
    }
}
