import * as THREE from 'three.js';
import {Phrase} from '../lib/phrase';
import {CSS3DRenderer} from '../renderers/CSS3DRenderer';
import {TrackballControls} from '../controls/TrackballControls';

const DEFAULT_PHRASE = 'This is something really cool!';

export class InteractivePage {
    scene = null;
    camera = null;
    renderer = null;
    element = null;
    domElement = null;
    objects = [];
    phraseInstance = null;
    _phrase = null;

    constructor(phrase = DEFAULT_PHRASE, hostDomElement) {
        this.domElement = hostDomElement;
        this._phrase = phrase;

        this.scene = new THREE.Scene();
        this.camera = new THREE.PerspectiveCamera( 40, window.innerWidth / window.innerHeight, 1, 10000 );
        this.camera.position.z = 30000;

        this.renderer = new CSS3DRenderer();
        this.renderer.setSize(window.innerWidth, window.innerHeight);


        this._init();
    }

    set phrase(phrase) {
        this._phrase = phrase;
        this.phraseInstance.updateText(phrase);
    }

    _init() {
        this.domElement.appendChild( this.renderer.domElement );
 
        this.phraseInstance = new Phrase({
            scene: this.scene,
            text: this._phrase,
            onUpdate: this._render,
        });

        this._configureControls();
    }

    _configureControls() {
        const controls = new TrackballControls(this.camera, this.renderer.domElement);
        controls.minDistance = 5000;
        controls.maxDistance = 60000;
        controls.addEventListener('change', this._render);
    }

    _animate() {
        const animate = () => {
            requestAnimationFrame( animate );
            this.phraseInstance.update();
        };
        animate();
    }

    _render = () => {
        this.renderer.render( this.scene, this.camera );
    }

    run() {
        this._animate();
    }
}

