import * as THREE from 'three.js';
import { CSS3DObject } from '../renderers/CSS3DRenderer';

export class Letter {
    _letter = null;
    _scene = null;
    _css3DObject = null;
    _object3d = null;
    _className = '';
    _column = 0;
    _row = 0;

    _height = 35;
    _width = 15;

    big = 4000;
    small = 2000;

    x_offset = 200;
    y_offset = 390;

    constructor({scene, letter, className, column, row}) {
        this._letter = letter;
        this._scene = scene;
        this._column = column;
        this._row = row;
        this._className = className || 'tile';
        this._ignore = letter.trim().length === 0;
        this._buildObject();
    }

    _getClassNames() {
        const classNames = [this._className];
        if (this._ignore) {
            classNames.push('hide');
        }

        this._row % 2 === 0 ? classNames.push('odd') : classNames.push('even');
        return classNames.join(' ');
    }

    _getDomElement() {
        const $el = document.createElement('div');
        $el.className = this._getClassNames();


        const $letter = document.createElement('div');
        $letter.className = 'letter';
        $letter.textContent = this._letter;
        $el.appendChild($letter);

        return $el;
    }

    _buildObject() {
        this._css3DObject = this._createCss3DObject();
        this._object3d = this._createObject3D();

        this._scene.add(this._css3DObject);
    }

    _createCss3DObject() {
        const $element = this._getDomElement();

        const object = new CSS3DObject( $element );
				object.position.x = Math.random() * this.big - this.small;
				object.position.y = Math.random() * this.big - this.small;
				object.position.z = Math.random() * this.big - this.small;

        return object;
    }

    _createObject3D() {
        const object = new THREE.Object3D();
				object.position.x = ( this._column * this.width ) - this.x_offset;
				object.position.y = - ( this._row * this.height ) + this.y_offset;

        return object;
    }

    destroy() {
        this._scene.remove(this._css3DObject);
    }

    get height() {
        return this._height;
    }

    get width() {
        return this._width;
    }

    get data() {
        return {
            css3DObject: this._css3DObject,
            object3d: this._object3d,
            instance: this,
        };
    }
}
